<template>
  <div>
    <section class="hero">
      <div class="hero-body">
        <div class="container">
          <div class="columns is-left">
            <div class="column is-2" />
            <div class="column is-two-fifths">
              <div class="is-cwhite-2 notification message">
                <strong class="is-size-5">欢迎注册</strong>
                <p class="is-size-7">请输入必要的信息,协助我们为你注册</p>
                <hr class="margin-05 has-background-cgray-1" />
                <b-field type="is-cwhite-1">
                  <strong class="mt-2 mr-4">账号:</strong>
                  <b-input v-model="registerinfo.username" expanded icon="account" />
                </b-field>
                <b-field type="is-cwhite-1">
                  <strong class="mt-2 mr-4">密码:</strong>
                  <b-input v-model="registerinfo.password" expanded icon="lock" type="password" password-reveal />
                </b-field>
                <b-field type="is-cwhite-1">
                  <strong class="mt-2 mr-4">昵称:</strong>
                  <b-input v-model="registerinfo.nickname" expanded icon="lock" />
                </b-field>
                <b-field type="is-cwhite-1">
                  <strong class="mt-2 mr-4">邮箱:</strong>
                  <b-input v-model="registerinfo.email" expanded icon="email" type="email" />
                </b-field>
                <hr class="margin-05" />

                <div>
                  <b-button expanded type="is-cblue-3" class="mt-3" @click="register()">注册</b-button>
                  <p class="has-text-centered">
                    已经有账号? 点击
                    <a href="/login">这里</a>
                    登陆
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { postFunc } from '@/scripts/internal/httpFunc'
export default {
  name: 'Register',
  data() {
    return {
      /**
       * 注册用户数据
       */
      registerinfo: {
        username: '',
        password: '',
        nickname: '',
        email: ''
      }
    }
  },
  computed: {},
  mounted() {},
  methods: {
    /**
     * 用户注册
     */
    register() {
      const that = this
      postFunc('/user/register', this.registerinfo).then(result => {
        // 获取数据
        const data = result
        if (data.resstatus === true) {
          // 用户等验证成功
          that.$store.commit('updateUserInfo', data) // 提交
          that.$router.push('/') // 跳转
        } else {
          // 用户不存在或错误,弹窗展示
          that.$buefy.dialog.alert({
            title: '错误',
            type: 'is-cred-2',
            message: data.meesage
          })
        }
      })
    }
  }
}
</script>

<style scoped>
.margin-05 {
  margin: 0.5rem;
}

.padding-10 {
  padding: 10rem 1.5rem;
}
</style>
